.container {
  width: 100%;
  height: 100%;
  background: #242424;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.controls-module {
  position: absolute;
  top: 50px;
  right: 50px;
}

.controls-global {
  flex-direction: column;
  display: flex;
  position: absolute;
  top: 50px;
  left: 50px;
}

.controls__button {
  cursor: pointer;
  color: #fff;
  height: 36px;
  word-spacing: 0;
  letter-spacing: .0892857em;
  text-transform: uppercase;
  min-width: 64px;
  text-align: center;
  background: #6200ee;
  border: none;
  border-radius: 4px;
  outline: none;
  margin: 20px;
  padding: 0 16px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.15;
  text-decoration: none;
  transition: box-shadow .28s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .2), 0 2px 2px rgba(0, 0, 0, .14), 0 1px 5px rgba(0, 0, 0, .12);
}

.controls__button:hover {
  background: #5600cf;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .2), 0 4px 5px rgba(0, 0, 0, .14), 0 1px 10px rgba(0, 0, 0, .12);
}

.canvas {
  width: 100%;
  height: 100%;
  background: #242424;
}

